/*полосa прокрутки для всей страницы*/
::-webkit-scrollbar {
    width: 5px;
    // background-color: #ffffff;
    background: linear-gradient(9deg, rgba(7, 0, 36, 1) 29%, rgba(111, 0, 255, 1) 100%);

    &-thumb {
        background-color: #3d3b3b;
        border-radius: 50px;
    }
    &-track {
        -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
    }
  }